@import "components/m_header";
@import "components/m_asside";
@import "components/m_footer";

@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-4{
            .product{
              &__slider{
                .itemSlider{
                  li{
                    width: 290px;
                    height: 290px;
                  }
                }
                .nav{
                  li{
                    &:nth-child(3n+1){
                      margin: 14px 14px 14px 0!important;
                    }
                    &:nth-child(4n+1){
                      margin: 14px;
                    }
                  }
                }
              }
            }
          }
          .col-md-9{
            .hologramOptimization{
              .hologram{
                .hologramPreview{
                  height: 285px;
                  .layer-3{
                    .bg{
                      ul{
                        .userLogo{
                          img{
                            width: 95px;
                            height: 110px;
                          }
                        }
                      }
                    }
                  }
                }
              }
              .hologramOptions{
                .layer-1,
                .layer-2,
                .layer-3{
                  .printingNumbering,
                  .printingLogo,
                  .printingText{
                    .name{
                      input{
                        width: 140px;
                      }
                    }
                    .fontSize{
                      p{
                        margin: 0;
                      }
                    }
                    .fontWeight{
                      p{
                        margin: 0 8px 0 100px;
                      }
                    }
                  }
                  .selectFile{
                    input + label{
                      margin: 0 35px 0 0;
                    }
                  }
                }
              }
            }
          }
          .col-md-12{
            .product{
              &__alsoViewed{
                .cart{
                  ul{
                    .inCart{
                      width: 53%;
                    }
                  }
                  .hover{
                    width: 216.19px;
                    bottom: -133px;
                  }
                }
              }
            }
          }
        }
      }
    }
    .sliderPopup{
      .container{
        .row{
          .col-lg-12{
            P{
              #close{
                top: -20px;
                right: -30px;
              }
            }
            .popupSlider{
              li{
                img{
                  width: 425px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .aside{
              display: none;
            }
          }
          .col-sm-12{
            .product{
              &__slider{
                .itemSlider{
                  li{
                    width: 280px;
                    height: 280px;
                  }
                }
              }
              &__alsoViewed{
                .cart{
                  ul{
                    .inCart{
                      width: 56%;
                    }
                    .heart,
                    .exchange{
                      width: 36.5px;
                    }
                  }
                  &:hover{
                    border-bottom-color: #78afeb;
                    .hover{
                      display: none;
                    }
                  }
                }
              }
            }
            .aside{
              margin: 35px 0 0;
              display: flex;
            }
          }
        }
      }
    }
    .sliderPopup{
      .container{
        .row{
          .col-sm-12{
            .popupSlider{
              li{
                img{
                  width: 80%;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px), screen and (max-device-width: 767px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .product{
              margin: 35px 0 0;
              &__slider{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                .nav{
                  li{
                    margin: 14px 7px;
                    &:nth-child(3n+1),
                    &:nth-child(4n+1){
                      margin: 14px 7px!important;
                    }
                  }
                }
              }
              .customize{
                width: 100%;
              }
              &__nav{
                ul{
                  display: flex;
                  justify-content: center;
                }
              }
              &__headline{
                p{
                  text-align: center;
                }
              }
              &__options{
                display: flex;
                flex-direction: column;
                align-items: center;
              }
              &__quantity{
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                .customQuantity{
                  width: 70%;
                }
              }
              .social{
                ul{
                  li{
                    .desktop{
                      display: none;
                    }
                    .mobile{
                      display: block;
                    }
                  }
                }
              }
              &__alsoViewed{
                flex-wrap: wrap;
                align-items: center;
                .cart{
                  width: 45%;
                  a{
                    text-align: center;
                    img{
                      width: 230px;
                      height: 220px;
                    }
                    span{
                      height: 70px;
                    }
                  }
                  ul{
                    .inCart{
                      width: 68%;
                    }
                    .heart,
                    .exchange{
                      width: 50px;
                    }
                  }
                  &:hover{
                    border-bottom-color: #78afeb;
                    .hover{
                      display: none;
                    }
                  }
                }
              }
            }
            .hologramOptimization{
              flex-direction: column;
              .hologram,
              .hologramOptions{
                width: 100%;
                .hologramPreview{
                  width: 325px;
                }
              }
              .hologram{
                padding: 0;
                ul{
                  li{
                    .arrowUp,
                    .arrowDown{
                      display: none;
                    }
                  }
                }
              }
              .hologramOptions{
                padding: 0 10px;
                .layer-1,
                .layer-2,
                .layer-3{
                  .printingNumbering,
                  .printingLogo,
                  .printingText{
                    justify-content: space-around;
                    .name{
                      width: 100%;
                      input{
                        width: 100%;
                      }
                    }
                    .selectFile{
                      padding: 0;
                    }
                    .fontSize{
                      width: unset;
                    }
                    .fontWeight{
                      p{
                        margin: 0 8px 0 0;
                      }
                    }
                  }
                }
                .layer-1{
                  .printingNumbering{
                    .name{
                      display: flex;
                      justify-content: space-between;
                      align-items: center;
                      input{
                        width: 40%;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .sliderPopup{
      .container{
        .row{
          .col-lg-12{
            p{
              font-size: 1em;
              #close{
                top: -15px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 650px), screen and (max-device-width: 650px){
  body{
    .content{
      .row{
        .col-xs-12{
          .hologramOptimization{
            .hologramOptions{
              .layer-1,
              .layer-2,
              .layer-3{
                .printingNumbering,
                .printingLogo,
                .printingText{
                  .selectFile{
                    flex-wrap: wrap;
                    justify-content: space-around;
                    input + label{
                      margin: 0 0 10px 0;
                      width: 100%;
                    }
                  }
                  .color{
                    width: 100%;
                    justify-content: center;
                  }
                  .fontSize,
                  .fontWeight{
                    width: 50%;
                    justify-content: center;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

